.about-title {
  font-size: 1.25rem;
  font-weight: 600;
}

.about-image {
  width: 100%;
  height: 200px;
  border: 1px solid rgba(154, 153, 153, 0.8);
  box-shadow: 1px 1px 8px 0px #3e3e3e;
  border-radius: 1rem;
}

// mobile layout
.about-grid {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  gap: 1rem;
}

// //tablet and larger layout
@media screen and (min-device-width: 750px) {
  .about-grid {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 1rem;
  }
}
